<script>
export default {
  name: 'FindTheNumber',
  components: {
  },
  data() {
    return {
      color:{},
      colors:{},

      colorsEasy:[
        {0:"#ff2c2c",1:"#ff2c4c"},
        {0:"#e91e63",1:"#F44336"},
        {0:"#3f51b5",1:"#3F518E"},
        {0:"#8bc34a",1:"#8BC385"},
        {0:"#9E9E9E",1:"#9EA0AE"},
        {0:"#ff5722",1:"#FF5764"},
        {0:"#673ab7",1:"#673A93"},
        {0:"#03a9f4",1:"#2196f3"},
        {0:"#2196f3",1:"#03a9f4"},
        {0:"#009688",1:"#00966A"},
        {0:"#b53f3f",1:"#B53F5B"},
        {0:"#b0d420",1:"#B0D476"},
        {0:"#20d4aa",1:"#32f5c7"},
        {0:"#d420c6",1:"#ec2dde"},
        {0:"#2026d4",1:"#262df1"},
      ]
    }
  },
  created() {
    this.callAddColor()
  },
  methods: {
    clickcolor(item){
      if (item == 1) {
        console.log("musab")
      } else {
        console.log("ahmed")
      }

      this.callAddColor();
    },
    callAddColor(){
      //this.addColor()
      this.addRandomColor()
    },
    addColor(){
      this.getColor()
    },
    addRandomColor(){
      this.colors={};
      for(var a=[],i=0;i<36;i++)a[i]=0;

      a[Math.floor(Math.random() * a.length)] = 1;

      var temp , current , top = a.length;
      if(top) while (--top) {
        current = Math.floor(Math.random() * (top + 1));
        temp = a[current]
        a[current] = a[top];
        a[top] = temp;
      }
      
      for (let n = 0; n < a.length; n++) {
        this.colors[n] = a[n];
      }

      this.color = this.getColor();
    },

    getColor(){
      var color = this.colorsEasy[Math.floor(Math.random() * this.colorsEasy.length)];
      return color;
    }
  },
}
</script>

